@import url('https://fonts.googleapis.com/css2?family=Lato&family=Montserrat:wght@500&family=Open+Sans:wght@300&family=Playfair+Display:wght@500&family=Roboto+Slab:wght@500&display=swap');

* {
  color: #fff;
  font-family: 'Open Sans', sans-serif;

}

body {
  margin: 0px;
  padding: 0px;
}

nav {
  border-top: solid 0.5px rgba(255, 255, 255, 0.5);
  padding: 10px;
  margin: 10px;
}

.history {
  padding: 10px;
  margin: 10px;
  height: 100%;
}

.history li {
  list-style-type: none;
  padding: 15px 0;
  cursor: pointer;
}

.app {
  background-color: #343141;
  display: flex;
}

.side-bar {
  background-color: #202123;
  height: 100vh;
  width: 244px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  border: solid 0.5px rgba(255, 255, 255, 0.5);
  background-color: transparent;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;

}

.main {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.info {
  color: rgba(255, 255, 255, 0.5);
  font-size: 11px;
  padding: 10px;
}

.input-container {
  position: relative;
  width: 100%;
  max-width: 650px;
}

.buttom-section {
  width: 100%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

input {
  width: 100%;
  border: none;
  background-color: rgba(255, 255, 255, 0.05);
  padding: 12px 15px;
  border-radius: 5px;
  box-shadow: rgba(0,0,0,0.05) 0 54px 55px,
    rgba(0,0,0,0.05) 0 -12px 30px,
    rgba(0,0,0,0.05) 0 4px 6px,
    rgba(0,0,0,0.05) 0 12px 3px,
    rgba(0,0,0,0.05) 0 -3px 5px;

}

input:focus {
  outline: none;
}

#submit {
  position: absolute;
  bottom: 15px;
  right: 0;
  cursor: pointer
}

.feed {
  overflow-y: auto;
  width: 100%;
  padding: 0;
}

.feed li {
  display: flex;
  background-color: #444654;
  width: 100%;
  padding: 20px 0 20px 50px;
  margin: 20px 0;
  box-sizing: border-box;
}

.feed p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  text-align: left;
}

.feed p.role {
  min-width: 100px;
}

@media (max-width: 768px) { 
  .side-bar {
    width: 100%; 
    height: auto; 
  }

  .main {
    width: 100%; 
    height: auto; 
  }

  .input-container,
  .button-section,
  .feed {
    width: 90%; 
    max-width: none; 
  }

  .feed li {
    padding: 10px 0 10px 20px;
  }
}
